
* {
    margin: 0;
    box-sizing: border-box;
    body {
        width: 100vw;
        height: 160vh;
        backdrop-filter: contrast(0.972) blur(9px) saturate(0.9);
        background-image:url(../../public/header.png);
        background-attachment: fixed;
        background-size: cover;
        background-repeat: no-repeat;
        .headerBg {
            border-top: 5px solid;
            border-bottom: 5px solid;
            border-color: rgba(208, 221, 233, 0.8);
            background-color: rgb(203 214 218 / 87%);;
            backdrop-filter: blur(10px);
            position: absolute;
            top: 10%;
            padding: 2rem;
            width: 100%;
            .headerWrapper {
                display: flex;
                flex-direction: column;
            }
            .headerTitle {
                color: #0d4842b5;
                font-size: 3rem;
                margin: auto;
                width: fit-content;
                padding: 1rem 0 2rem;
                text-align: center;
            }
            .navBar {
                display: flex;
                flex-direction: row;
                justify-content: space-evenly;
            }
            //the styling of the specific buttons is described in the _btn
        }
    }
}